import './App.css';
import React from "react";
import { Switch, Router, Route, Redirect } from "react-router-dom";
import history from "./history";

import Header from './components/header/Header';
import Home from "./components/home/Home";
import Dashboard from "./components/dashboard/Dashboard";
import Sidebar from './components/Sidebar/Sidebar';
import TokenSwap from './components/TokenSwapScreen/TokenSwap';
import SidePanel from './components/Sidebar/SidePanel';
import TermsConditions from './components/TCItem/TermsConditions';
import Footer from './components/Footer/Footer';



export default function App() {
  const routes = (
    <Switch>
      <Route path={process.env.PUBLIC_URL + '/swap'} exact>
        <TokenSwap/>
      </Route>
      <Route path={process.env.PUBLIC_URL + '/termsandconditions'} exact>
        <TermsConditions/>
      </Route>
      <Route path={process.env.PUBLIC_URL + '/'} exact>
        <Home />
      </Route>
      <Route  path="/dashboard" exact>
        <Dashboard />
      </Route>
      
      <Redirect to="/" />
    </Switch>
  );

  return (
    <>
      <Router history={history}>
        <Sidebar/>
        <Header />
       <div >
       {routes} 
       </div>
      </Router>
      <Footer/>
    </>
  );
}

