import React, {
  useState,
  useReducer,
  useEffect,
  useCallback,
  createContext,
} from "react";

import { Web3Reducer } from "./reducer";

// WEB3 CONNECTION PACKAGES
import Web3 from "web3";
import Web3Modal from "web3modal";
import WalletConnectProvider from "@walletconnect/web3-provider";
import Torus from "@toruslabs/torus-embed";
import Authereum from "authereum";

import { ERC20_ABI, OLD_IAG_ADDRESS, SWAPPER_ADDRESS, SWAPPER_ABI } from "./constants";

import notify from "../utils/notify";

import history from "../history";
import BigNumber from 'bignumber.js'

let web3;

const initialState = {
  loading: true,
  account: null,
  networkId: null,
  contracts: {
    token: null,
  },
};

export const Web3Context = createContext(initialState);

export const Web3Provider = ({ children }) => {
  const [state, dispatch] = useReducer(Web3Reducer, initialState);
  const [showModal, setShowModal] = useState(false);


  const { account, contracts } = state;

  // STATE MANAGEMENT
  const setAccount = (account) => {
    dispatch({
      type: "SET_ACCOUNT",
      payload: account,
    });
  };

  const setContracts = (contracts) => {
    dispatch({
      type: "SET_CONTRACTS",
      payload: contracts,
    });
  };

  const setNetworkId = (networkId) => {
    dispatch({
      type: "SET_NETWORK_ID",
      payload: networkId,
    });
  };

  const setProtocol = async (accounts) => {
    setAccount(accounts[0]);

    // Contract Instances
    window.token = new web3.eth.Contract(ERC20_ABI, OLD_IAG_ADDRESS, {
      from: accounts[0],
    });
    window.swapper = new web3.eth.Contract(SWAPPER_ABI, SWAPPER_ADDRESS);
    setContracts({
      token: window.token,
      swapper: window.swapper,
    });
  };

  // === HELPERS === //
  const logout = () => {
    dispatch({
      type: "CLEAR_STATE",
      payload: initialState,
    });
    localStorage.setItem("WEB3_CONNECT_CACHED_PROVIDER", null);
    history.push("/");
  };
  const toWei = (value) => web3.utils.toWei(String(value));
  const fromWei = (value) => Number(web3.utils.fromWei(String(value)));
  const toBN = (value) => new web3.utils.BN(String(value));

  // === MAIN FUNCTIONS === //

  // Connect Web3 wallet and set state (contracts, roles, account)
  const connectWeb3 = useCallback(async () => {
    // Web3 Modal
    const providerOptions = {
      walletconnect: {
        package: WalletConnectProvider, // required
        options: {
          infuraId: "36bbdc3ed5bd449fad0374a2e07b850a", // required
        },
      },
      torus: {
        package: Torus, // required
        options: {
          networkParams: {
            host: "https://mainnet.infura.io/v3/36bbdc3ed5bd449fad0374a2e07b850a", // optional
            networkId: 1, // optional
          },
          config: {
            buildEnv: "production", // optional
          },
        },
      },
      authereum: {
        package: Authereum,
      },
    };

    try {
      const web3Modal = new Web3Modal({
        network: "mainnet", // optional
        cacheProvider: true, // optional
        providerOptions, // required
        theme: "light",
      });

      const provider = await web3Modal.connect();

      web3 = new Web3(provider);
      window.web3 = web3;

      const _accounts = await web3.eth.getAccounts();
      await setProtocol(_accounts);
      console.log("Connected Account: ", _accounts[0]);

      const networkId = await web3.givenProvider.networkVersion;
      setNetworkId(networkId);
      if (Number(networkId) !== 1) {
        //alert("Please connect to etherum mainnet network");
        history.push("/");
        return;
      }

      notify("success", "connected web3 wallet", 1500);

      window.ethereum.on("chainChanged", () => {
        document.location.reload();
      });

      //If accounts change
      window.ethereum.on("accountsChanged", (accounts) => {
        setProtocol(accounts);
      });
    } catch (error) {
      notify("error", "Could not connect to web3!");
      //console.log(error.message);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const getBalances = async () => {
    try {
      const balanceIAG = await contracts.token.methods
        .balanceOf(account)
        .call();
      const balanceETH = await web3.eth.getBalance(account);

      return { ETH: fromWei(balanceETH), IAG: fromWei(balanceIAG) };
    } catch (error) {
      notify("error", error.message);
      console.log(error.message);
    }
  };

  const getAllowances = async () => {
    try {
      const balanceIAG = await contracts.token.methods
        .allowance(account, SWAPPER_ADDRESS)
        .call();
      return fromWei(balanceIAG);
    } catch (error) {
      notify("error", error.message);
      console.log(error.message);
    }
  };


  const swapToken = async (amount) => {
    try {
      const allowanceIAG = await contracts.token.methods
        .allowance(account, SWAPPER_ADDRESS)
        .call();
      if(fromWei(allowanceIAG) >= amount) {
        const roundedAmount = new BigNumber(amount - 0.000001).multipliedBy(new BigNumber(10).pow(18))
        const swappingTx = await contracts.swapper.methods
          .SwapNow(roundedAmount)
          .send({ from: account });
        notify("success", "Successfully swapped");
        document.location.reload();

        return swappingTx;
      } else {
        const allow = await contracts.token.methods
        .approve(SWAPPER_ADDRESS, new BigNumber(amount).multipliedBy(new BigNumber(10).pow(18)))
        .send({ from: account });
        notify("success", "Successfully Approved");
      }
    } catch (error) {
      notify("error", error.message);
      console.log(error.message);
    }
  };

  // Connect web3 on app load
  useEffect(() => {
    connectWeb3();
  }, [connectWeb3]);

  return (
    <Web3Context.Provider
      value={{
        ...state,
        connectWeb3,
        logout,
        toWei,
        fromWei,
        toBN,
        getBalances,
        getAllowances,
        swapToken
      }}
    >
      {children}
    </Web3Context.Provider>
  );
};
